                <div class="delegation_contain">
                    <span class="delegation_title"></span>
                    <div class="delegation_content">
                        <div class="name_style" style="margin-top: 0.3rem;">
                            <p>:<span class="name_style userName">{{orderData.mRealName}}</span></p>
                            <p>:<span class="name_style userCard">{{orderData.mIdCard}}</span></p>
                        </div>
    
