<template>
	<div id="aboutUs">
		<CommonHeader titles="" :imgChangeFlags="true" :backFlags="false"></CommonHeader>
		<section>
			<div class="aboutus-con">
				<h1 class="fontSize30 color-black marginBottom30"></h1>
				<p>
					
				</p>
			</div>
		</section>
	</div>
</template>

<script>
	import CommonHeader from '../../components/Common_header.vue'
	export default{
		components: { CommonHeader },
		data(){
			return{
				
			}
		},
		mounted: function () {
		  this.$nextTick(function () {
		    this.getBodyHeight();
		  })
		},
		methods:{
			getBodyHeight(){
				var bodyheight=$(window).height();
				console.log(bodyheight);
				$(".aboutus-con").css("height",((bodyheight*2/100)-1.18)+"rem");
			}
		},
	}
</script>

<style>
	.aboutus-con{margin:0.3rem 0.3rem 0;background: #FFFFFF;text-align: center;padding: 0.34rem 0.2rem;}
	.aboutus-con p{text-indent: 0.5rem;text-align: left;font-size: 0.24rem; color: #5e5e5e;}
</style>


